@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
*{
    margin: 0%;
    padding: 0;
    box-sizing: border-box;
    overflow: hidden;
}

body{
    height: 100vh;
    width: 100%;
}

.back{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    background-image: url('../../../assests/doodle.png');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
}

div[class=login_box]{
    flex: 3;
    display: flex;
    justify-content: center;
    align-items: center;
}

div[id=box]{
    height: 33.125rem;
    width: 46.875rem;
    background: #1E2126;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

div[id=welcome]{
    width: 46.875rem;
    height: 6rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

h2{
    color: #FFFFFF;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
}

div[id=login_boxs]{
    width: 46.875rem;
    height: 23.438rem; 
    display: flex;
    flex-direction: column;
}

p{
    margin-top: 1rem;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    color: #FFFFFF;
}

div[id=input_box_log]{
    width: 33rem;
    height: 3.4rem;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 50px;
    margin-top: 1rem;
}

div[id = flex_col_log]{
    display: flex;
    flex-direction: column;
}

input[id=username]{
    width: inherit;
    height: inherit;
    border: none;
    padding-left: 1.5rem;
    font-size: large;
    background-color: transparent;
    color: #FFFFFF;
}

input[id=pass]{
    width: inherit;
    height: inherit;
    border: none;
    padding-left: 1.5rem;
    font-size: large;
    background-color: transparent;
    color: #FFFFFF;
}

button[id=login_button]{
    margin-top: 2rem;
    width: 33rem;
    height: 3.4rem;
    background: rgba(209, 163, 0, 0.9);
    border-radius: 50px;
    border: none;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    text-align: center;

    color: #FFFFFF;
}

button[id=dont]{
    margin-top: 0.5rem;
    width: 33rem;
    height: 2rem;
    border: none;
    background-color: transparent;
    opacity: 0.6;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: rgba(255, 255, 255, 0.7);
}

div[class=login_banner]{
    flex: 1;
}   

div[id=banner]{
    height: 11.563rem;
    width: 11.563rem;
    background: #D1A300;
    border-radius: 0px 0px 50px 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

p[id=banner_log]{    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.695em;

    color: #FFFFFF;
}